    <view class="zai-box">
        {{msg}}
    </view>

</template>

<script>
	
    export default {
		components:{
	
		},
        data() {
            return {
				msg: ''
            };
        },
		created() {
			const url = window.location.href
			if(url.indexOf("qyweixin=1")>-1){
				let end = url.lastIndexOf("?");
				let msg = url.substring(url.lastIndexOf("&msg=")+5,end);
				this.msg = msg;
			}
		},
		onLoad:function(){
			
		},
        methods: {
			
        }
    }
</script>

